@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f7f7f8; 
}

*{
  font-family: Raleway;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}

button {
  border-radius: 10px;
  border: none;
  text-align: center;
  height: 30px;
  cursor: pointer;
  background-color: #772ce8;
  color:white;
  font-family: Raleway;
  font-weight: 600;
}
button:hover {
  
  border-radius: 10px;
  border: none;
  color: black;
  box-shadow: 0px 3px 3px black ;
}
button:active {
  box-shadow: none;
}
.App {
  max-width: 1200px;
  margin: 0 auto;
}


.header {
    height: 80px;
    background-color: white;
    box-shadow: 0px 3px 3px grey ;
    padding: 10px;
}
.header div {
    margin-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}


.login input {
    padding: 5px;
    border:2px solid black;
    border-radius: 4px;
    outline: none;
}

.logout {
    margin-left: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 200px;
}
.logout div {
    margin: 0;
    font-weight: 700;
}

.autorization a{
    text-decoration: none;
    font-weight: 500;
    font-size: 20px;
}

.autorization a:hover {
    text-decoration: none;
    font-weight: 700;
    font-size: 20px;
}
.autorization a:active {
    color: black;
}
.autorization a:first-child::after {
    content: '';
    border: 1px solid #772ce8;
    margin-left: 10px;
}
.auth {
    margin: 0 auto;
    height: 300px;
    background-color: white;
    width: 300px;
    margin-top: 100px;
    box-shadow: 0px 3px 3px grey ;
}

.auth form {
    display: flex;
    flex-direction: column;
    width: 200px;
    margin: 0 auto;
    grid-row-gap: 20px;
    row-gap: 20px;
    padding-top: 20px;
}

.auth form input {
    padding: 5px;
    border:none;
    border-bottom: 1px solid black;
    outline: none;
    background-color: white;
}

.auth .error {
    text-align: center;
    margin-top: 10px;
    color: red;
}

.auth .success {
    text-align: center;
    margin-top: 10px;
    color: green;
}

.auth_title {
    text-align: center;
    padding-top: 30px;
    font-size: 18px;
    font-weight: 700;
    color:#772ce8;
}

.chat {
    margin: 0 auto;
    height: 400px;
    background-color: white;
    max-width: 800px;
    margin-top: 100px;
    box-shadow: 0px 3px 3px grey ;
    border-radius: 20px;
    padding: 10px;
}

.chat_title {
    border-radius: 5px;
    height: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 3px 3px grey ;
    background-color: #772ce8;
    color: white;
    position: relative;
    z-index: 2;
    font-weight: 600;
}
.chat_messages {
    padding: 5px;
    overflow-y: scroll;
    height: 310px;
    margin-top: 1px;
}

.chat_footer {
    display: flex;
    flex-direction: row;
    height: 30px;
    padding-top: 10px;
}
.chat_footer input {
    width: 100%;
    border: none;
    outline:none;
}

.chat_footer input:focus:hover:active {
    width: 100%;
    border: none;
}

.chat_messages::-webkit-scrollbar {
    width: 10px;
    position: relative;
    z-index: 1;
  }
  
  .chat_messages::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #772ce8;
  }
  
  .chat_messages::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);
    border-radius: 10px;
    background-color: #f9f9fd;
  }

  .chat_messages .user {
      font-weight: 700;
      max-width: 400px;
  }
  .chat_messages .message {
    margin-top: 5px;
    padding: 5px;
    word-wrap: break-word;
    max-width: 400px;
    border-radius: 10px;
    background-color: #f7f7f8;
    border:1px solid black;
    margin-bottom: 5px;
}

.me {
    text-align: right;
    margin-left: auto;
}

.chat_messages .message.me {
    background-color:#D4E7FA;
}
